<template>
  <div class="my">
    <div class="header clearfix">
      <div class="install">
        <van-icon name="setting-o" color="white" @click="goPage('Account')" size="20px" />
      </div>
      <div v-if="joinLogin">
        <div class="sea">
          <span class="wave"></span>
          <span class="wave"></span>
          <span class="wave"></span>
        </div>
        <!-- 简介 -->
        <transition name="slide-fade">
          <div class="introduction" v-if="IntroductionShow">
            <div class="introduction_bg">
              <div class="a"></div>
              <div class="b"></div>
              <div class="c"></div>
              <div class="d"></div>
              <div class="desc">{{userInfo.desc}}</div>
            </div>
          </div>
        </transition>

        <!-- 头像 -->
        <div class="picture" @click="introduction()">
          <img class="auto-img" :src="userInfo.userImg" alt />
        </div>
        <!-- 昵称 -->
        <transition name="userdetail-fade">
          <div class="userdetail" v-if="!IntroductionShow">
            <div class="name">{{userInfo.nickName}}</div>
            <div class="member clearfix">
              <div class="member_p fl">
                <img class="auto-img" src="../../assets/images/会员.png" />
              </div>
              <span class="fl">普通会员&nbsp;0积分</span>
            </div>
          </div>
        </transition>
      </div>

      <div v-else>
        <div class="picture">
          <img
            class="auto-img"
            src="https://img11.360buyimg.com/jdphoto/s120x120_jfs/t21160/90/706848746/2813/d1060df5/5b163ef9N4a3d7aa6.png"
            alt
          />
        </div>
        <div class="login-text" @click="goPage('Login')">登 录</div>
      </div>
    </div>
    <!-- 订单状态 -->
    <div class="Orderselect clearfix">
      <div class="cart fl" v-for="(item,index) in cart " :key="index" @click="goRoutes(index)">
        <van-icon class="fl" :name="item.icon" color="black" size=".56rem" />
        <span>{{item.name}}</span>
      </div>
      <div class="cart fl" @click="goPage('OrderAll')">
        <span class="fl">全部订单</span>
        <van-icon class="fl" name="arrow" color="black" size=".63rem" />
      </div>
    </div>
    <!-- 工具 -->
    <div class="tool clearfix">
      <div class="title clearfix">
        <a>必备工具</a>
      </div>
      <div class="tool_cart fl" v-for="(item,index) in tool" :key="index">
        <div class="tool_picture">
          <img class="auto-img" :src="item.img" alt />
        </div>
        <span>{{item.title}}</span>
      </div>
    </div>

    <Banner />
    <Recommend />
  </div>
</template>

<script>
import Banner from "../../components/Banner.vue";
import Recommend from "../../components/Recommend.vue";
import "../../assets/css/my.less";

export default {
  data() {
    return {
      cart: [
        {
          icon: "balance-pay",
          name: "待付款",
          routeName: "OrderPay",
          active: 1,
        },
        {
          icon: "send-gift-o",
          name: "待收货",
          routeName: "OrderGoods",
          active: 2,
        },
        {
          icon: "sign",
          name: "已完成",
          routeName: "OrderEnd",
          active: 3,
        },
        {
          icon: "location-o",
          name: "我的地址",
          routeName: "MyAddress",
        },
        {
          icon: "star-o",
          name: "我的收藏",
          routeName: "Favorites",
        },
      ],
      tool: [
        {
          img:require("../../assets/images/m1.png"),
          title: "我的预约",
        },
        {
          img:require("../../assets/images/m2.png"),
          title: "我的资产",
        },
        {
          img:require("../../assets/images/m3.png"),
          title: "高价回收",
        },
        {
          img:require("../../assets/images/m4.png"),
          title: "小鹅火车票",
        },
        {
          img:require("../../assets/images/m5.png"),
          title: "应用推荐",
        },
        {
          img:require("../../assets/images/m6.png"),
          title: "小鹅机票",
        },
        {
          img:require("../../assets/images/m7.png"), 
          title: "小鹅酒店",
        },
        {
          img:require("../../assets/images/m8.png"),
          title: "闲置换钱",
        },
      ],
      IntroductionShow: false,
      //登录状态
      joinLogin: false,
      //用户信息
      userInfo: {},
    };
  },
  created() {
    this.getUserInfo();

    if (localStorage.getItem("__Tk")) {
      this.joinLogin = true;
    }
  },
  methods: {
    //跳转页面
    goPage(name) {
      if (name === "Login") {
        this.$router.push({ name });
      } else {
        if (!this.joinLogin) {
          this.$toast("请先登录");
        } else {
          this.$router.push({ name });
        }
      }
    },
    goRoutes(index) {
      let active = this.cart[index].active;
      if (localStorage.getItem("__Tk")) {
        this.$router.push({
          name: this.cart[index].routeName,
          query: { active },
        });
      } else {
        this.$toast("请先登录");
      }
    },
    //获取用户信息
    getUserInfo() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "http://www.kangliuyong.com:10002/findMy",
        params: {
          appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "A001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    introduction() {
      this.IntroductionShow = !this.IntroductionShow;
    },
  },
  components: {
    Banner,
    Recommend,
  },
};
</script>

<style lang="less" scoped>
</style>